form#blueprints .tabs-content.locked {
  position: relative;
}

form#blueprints .tabs-content.locked::before {
  content: "";
  position: absolute;
  z-index: 999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

#titlebar-button-delete.locked,
#titlebar-button-move.locked,
#titlebar-save.locked,
#admin-mode-toggle.locked {
  display: none !important;
}

